import { HTTPUtil as api } from '../fetch/api.js'
import { client as yh } from './client.js'

var logAddress = 'http://112.18.251.100:8001/yanhua';

function bi () {
 
}

var logObFormat = function ( o ){
    return JSON.stringify( o , function (k, v) {
        return typeof v === "number" ? v + "" : v;
    });
}


/****
 * 公共字段
 * site_id(站点ID)
 * device_id（终端设备ID）
 * user_id（用户ID）
 * log_type（日志类型）
 * v(日志版本)
 * model（终端硬件型号）
 * soft_v（终端软件版本号）
 * vip(用户属性)
 * root_id（导航栏ID）
 * province（用户省份）
 * create_time（触发时间）
 */
function  CommonParams (){
    this.site_id = yh.siteId;
    this.device_id = yh.device_id;
    this.userId = yh.userId;
    this.log_type = '2';
    this.v = '1';
    this.model = yh.model;
    this.soft_v = yh.soft_v;
    this.vip = yh.vip;
    this.province = "四川省";
    this.create_datetime = new Date().getTime() + '';
    this.bigdata_flag = '0';
    // this.ip = '';
    // this.root_id = '';
    // {
    //     site_id: yh.siteId,
    //     device_id: yh.device_id,
    //     user_id: yh.userId,
    //     log_type: '2',
    //     v: '1',
    //     model: yh.model,
    //     soft_v: yh.soft_v,
    //     vip: yh.vip,
    //     // ip:'',
    //     province: '四川省',
    //     // root_id: '', 
    //     create_time: new Date().getTime() + '',
    //     bigdata_flag: '0'
    // }
}
    // 启动
bi.start = function(type) {
        let logOb = new CommonParams();
        logOb.event = '102';
        logOb.start_type = type;
        console.log(logOb,'startstatstetastatatatstststst')
        logup(logObFormat(logOb))
    }
    // 退出
bi.end = function(type) {
    let logOb = new CommonParams();
    logOb.event = '103';
    logOb.time = new Date().getTime() - parseInt(JSON.parse(localStorage.getItem('app')).startTime);
    // logOb.quit_type = type
    console.log( logOb,'endenednednendenenen' )
    logup(logObFormat(logOb))
}

/****
 * 支付
 *
 * pkg_type 产品包类型
 * pkg_id 产品包ID
 * pkg_price 产品包价格
 * action 订购动作
 * order_msg 订购信息
 * parent_page_id 父级页面ID
 * parent_page_type 父级页面类型
 * preview 试看类型
 */
bi.order = function(jsonOb) {
    let logOb = new CommonParams()
    logOb.event = '201';
    logOb.pkg_type = jsonOb.pkg_type;
    logOb.pkg_id = jsonOb.pkg_id;
    logOb.operator_id = jsonOb.operator_id;
    // logOb.pkg_price = jsonOb.pkg_price
    // logOb.action = jsonOb.action;
    logOb.order_msg = jsonOb.order_msg;
    logOb.parent_page_id = jsonOb.parent_page_id;
    logOb.parent_page_type = jsonOb.parent_page_type;
    logOb.point = jsonOb.point;
    console.log( logOb,'orderorderorder' )
    logup(logObFormat(logOb))
}

/****
 * 点击订购按钮
 *
 * page_id 页面id
 * page_type 页面类型
 */
bi.orderClick = function (jsonOb) {
    console.log('bi 点击订购按钮日志');
    let logOb = new CommonParams;
    logOb.event = '202';
    logOb.page_id = jsonOb.page_id;
    logOb.page_type = jsonOb.page_type;
    console.log( logOb,'orderrooeroeoreoreoreoreo' )
    logup(logObFormat(logOb));
}

/****
 * 收藏页/历史页操作
 *
 * page_type 页面类型
 * click_id 被操作对象的id
 * click_type 当前动作类型
 * col_num 收藏的资产数
 */
bi.collectionHistory = function(jsonOb) {
    let logOb = new CommonParams()
    logOb.event = '5';
    logOb.page_type = jsonOb.page_type;
    logOb.cid = jsonOb.click_id;
    logOb.click_type = jsonOb.click_type
    // logOb.col_num = jsonOb.col_num;
    // console.warn(jsonOb)
    console.log( logOb,'colleceocelcelcecelecll' )
    logup(logObFormat(logOb))
}

//收藏专题和资产
/** 
 * click_type 收藏对象的类型 1. 点击资产 2. 点击专题 3. 点击演员(暂无) 4. 点击标签(暂无)*/ 
bi.collection = function (jsonOb) {
    console.log('bi 收藏日志');
    let logOb = new CommonParams();
    logOb.event = '4'; //事件代号
    logOb.click_type = jsonOb.click_type; //收藏对象的类型
    logOb.cid = jsonOb.cid; //收藏对象id
    logOb.collect = jsonOb.collect; //收藏动作  
    console.log( logOb,'collecleclecelececleclecelcelecleclecleclecellcelcelcel' )
    logup(logObFormat(logOb))
  }
  

/***
 *  点播
 * asset_id 资产壳ID
 * item_id  子集ID
 * qb_datetime 起播时间
 * zb_datetime 终止播放时间
 * qb_assettime 视频起播时间
 * zb_assettime 视频终播时间
 * duration 适配总时长
 * ep 集数
 * asset_from 资产来源
 * asset_type 资产类型
 * time 实际播放时长
 * error 错误信息
 * fee 是否收费
 * parent_page_type 父级页面类型
 * parent_page_id 父级页面ID
 * pos_id 推荐页位置
 * recmd_id 大数据推荐类型
 */
bi.vod = function(jsonOb) {
    let logOb = new CommonParams();
    logOb.event = '8';
    logOb.asset_id = jsonOb.asset_id;
    logOb.item_id = jsonOb.item_id;
    logOb.qb_datetime = jsonOb.qb_datetime;
    logOb.zb_datetime = jsonOb.zb_datetime;
    // logOb.qb_assettime = jsonOb.qb_assettime;
    // logOb.zb_assettime = jsonOb.zb_assettime;
    // logOb.duration = jsonOb.duration;
    logOb.ep = jsonOb.ep;
    // logOb.asset_from = jsonOb.asset_from;
    // logOb.asset_type = jsonOb.asset_type;
    logOb.time = jsonOb.time;
    logOb.error = jsonOb.error;
    logOb.fee = jsonOb.fee;
    logOb.parent_page_type = jsonOb.parent_page_type;
    logOb.parent_page_id = jsonOb.parent_page_id;
    logOb.pos_id = jsonOb.pos_id;
    logOb.isFullScreen = jsonOb.isFullScreen;
    logOb.recmd_id = jsonOb.recmd_id;
    console.log( logOb,'vodvodvodvodvodvodvovovovovovovo' )
    logup(logObFormat(logOb))
}

