/***
 * 筛选页键盘操作
 * 
 * event 事件代号
 * result 结果枚举
 * content 点击页面对应id
 */
bi.screeningKeyboard = function(jsonOb) {
    let logOb = new CommonParams();
    logOb.event = '3';
    logOb.result = jsonOb.result;
    logOb.content = jsonOb.content;
    logup(logObFormat(logOb))
}

/****
 * 筛选页键盘区操作
 * event 事件代号
 * result 当前动作完成的结果类型枚举 0、筛选后无结果 1、筛选有结果，点击筛选结果 2、不筛选，点击默认推荐内容
 * keyword 当前动作完成时的上报的内容字符串，以“|”隔开
 * cid 被点击的资产id 筛选无结果，cid可为空
 * cat_id 导航栏id，见epg接口cat_id，用于区分筛选入口
 */
bi.keyDoSelect = function (jsonOb) {
    console.log('bi 筛选日志');
    var logOb = new CommonParams();
    logOb.event = '14'; //事件代号
    logOb.result = jsonOb.result; //筛选结果
    logOb.keyword = jsonOb.keyword; //筛选关键词
    logOb.cid = jsonOb.cid; //被点击的资产id
    logOb.cat_id = jsonOb.cat_id; //导航栏id
    console.log(logOb,'seleseselselselselselselselselselsel')
  }


/****
 * 搜索页键盘区操作
 * event 事件代号
 * result 当前动作完成的结果类型枚举 0、搜索无结果 1、搜索有结果，点击搜索结果 2、不搜索，点击默认推荐内容
 * keyword 当前动作完成时的上报的内容字符串，以“|”隔开
 * click_type 被点击的对象类型 1、点击资产；2、点击专题（暂无）；3、点击演员；4、点击标签（暂无）；若有其他搜索内容，则增加代号，但是已有代号不可随意更改
 * cid 被点击的资产id 筛选无结果，cid可为空
 */
bi.keyDoSearch = function (jsonOb) {
    console.log('bi 搜索日志');
    var logOb = new CommonParams();
    logOb.event = '6'; //事件代号
    logOb.result = jsonOb.result; //搜索结果
    logOb.keyword = jsonOb.keyword; //搜索关键词
    logOb.click_type = jsonOb.click_type; //被点击的对象类型
    logOb.cid = jsonOb.cid; //被点击的资产id
    console.log(logOb,'loglogloglogologogogogogog')
    logup(logObFormat(logOb));
}
  


    // 上传
function logup(jsonOb) {
    // console.error('BI log:' + JSON.stringify(jsonOb))
    api.post(logAddress, jsonOb, function() {
        console.log('bi 日志发生完毕');
    })
}
export { bi }


