<template>
  <div id="app">
    <!-- 这里是需要keepalive的 -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive" />
    </keep-alive>
    <!-- 这里不会被keepalive -->
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
import { client as yh } from './js/client.js';
import { yst as logUp } from './js/yst.js';
import { HTTPUtil as api } from './fetch/api';
export default {
  name: 'app',
  beforeCreate () {
    var reg = new RegExp('(^|&)Ptype=([^&]*)(&|$)', 'i');
    var r = window.location.search.substr(1).match(reg);
    var cdn
    if (r!=null){
      cdn = unescape(r[2]);
      if (r==null||r[2]=="null"||r[2]==undefined||r[2]=='') {
        localStorage.setItem("cdnType",1);
        cdn = 1;
      } else {
        localStorage.setItem("cdnType",cdn);
      }
    } else {
      if (localStorage.getItem("cdnType")) {
        cdn = localStorage.getItem("cdnType");
      }else{
        localStorage.setItem("cdnType",1);
        cdn = 1;
      }
    };
    var userId = yh.getUserAccount();
    // var userId = "15228591751";
    // const deviceId = yh.getSTBDeviceId();
   
    let macs = yh.getSTBParameter(yh.STB_KEYS.MAC).split(':').join('');
    const deviceId = '2019062752' + macs;
    const mac = yh.getSTBParameter(yh.STB_KEYS.MAC);
    // const cdn = yh.getCdn(val?val:510100);
    // const cdn = this.getKv("Ptype");
    const DeviceOb = {
      reserve_group: 'yanhua',
      model: 'Android4.4',
      content_id: '3',
      terrace_id: cdn,
      operator_id: '2',
      apk_version: '0.0.0',
      brand: 'mbh',
      site_id: '24',
      user_id: userId,
      device_id: deviceId,
      soft_v: '2.0.001',
      sys_v: '4.4.2',
      mode: 'CM101',
      mac: mac,
      ip: '0:0:0:1',
      apk_version_name: '四川电竞',
      cdn: cdn
    };
    yh.setDeviceInfo(DeviceOb);
    logUp.init();
  },
  mounted () {
    console.log('app 已挂载');
  },
  methods: {
    // getKv (name){
    //     var reg = new RegExp("(^|&)" + name + "=([^&]*)(&|$)", "i");
    //     var r = window.location.search.substr(1).match(reg);
    //     if (r != null) return unescape(r[2]); return null;
    // }
  }
};
</script>
<style>
@import url(./font/iconfont.css);
</style>



